<template>
  <div class>
    <hooper
      :items-to-show="3"
      :infinite-scroll="true"
      :auto-play="false"
    >
      <slide
        v-for="inf in info"
        :key="inf.id"
        style="width:220px;"
      >
        <div class="card">
          <div class="m-1 p-1 d-flex align-items-center card-img-size">
            <img
              style="cursor: pointer"
              :src="`http://bohmann.shop${inf.images[0]}`"
              class="img-fluid"
              alt="..."
              @click="goToproduct(inf.id)"
            >
          </div>
          <div class="card-body p-1 d-flex align-items-center justify-content-center">
            <h5
              style="cursor: pointer"
              class="card-title text-center"
              @click="goToproduct(inf.id)"
            >
              {{ inf.name }}
            </h5>
          </div>
          <div class="m-1">
            <p class="card-text text-center">
              {{ inf.price }} ₴
            </p>
            <button
              type="button"
              class="btn btn-primary btn-sm btn-block"
              @click="addToCart(inf)"
            >
              Купить
            </button>
          </div>
        </div>
      </slide>
    </hooper>
  </div>
</template>

<script>
import { Hooper, Slide } from "hooper";
import "hooper/dist/hooper.css";

export default {
    components: {
        Hooper,
        Slide
    },
    props: ["info"],
    methods: {
        goToproduct(Id) {
            this.$router.push({ name: "product", params: { Pid: Id } });
        },
        addToCart(item) {
            this.$store.commit("addToCart", item);
            let found = this.$store.state.cart.find(
                product => product.id === item.id
            );
            found.inCart = true;
        }
    }
};
</script>

