<template>
  <b-row class="text-center account">
    <b-col
      cols="12"
      xl="12"
      class="border p-2"
    >
      <img
        src="../img/account-img/account-img.svg"
        alt="ak"
      >
      <h4
        class="cart-product__title"
        style="color:#14145E;"
      >
        <div v-if="log === 'false'">
          <LogReg />
        </div>
        <div v-if="log === 'true'">
          {{ user.name }}
        </div>
      </h4>
    </b-col>
    <div
      v-if="log === 'true'"
      class="sz"
    >
      <b-col
        xl="12"
        cols="12"
        class="d-flex align-items-center justify-content-start pt-4 pl-3 p-2 border-left border-right"
      >
        <img
          class="img-account"
          src="../img/account-img/account-cart.svg"
          alt="cart"
        >
        <router-link to="/basket">
          <h4 class="account">
            <a href="#">Корзина</a>
          </h4>
        </router-link>
      </b-col>
      <b-col
        xl="12"
        cols="12"
        class="text-left d-flex align-items-center justify-content-start pl-3 p-2 border-left border-right"
      >
        <img
          class="img-account"
          src="../img/account-img/account-favorite.svg"
          alt="FAVOURITE"
        >
        <router-link to="/favorite">
          <h4 class="account">
            <a href="#">Желаемое</a>
          </h4>
        </router-link>
      </b-col>
      <b-col
        xl="12"
        cols="12"
        class="d-flex align-items-center justify-content-start pl-3 p-2 border-left border-right"
      >
        <img
          class="img-account"
          src="../img/account-img/account-settings.svg"
          alt="SETTINGS"
        >
        <router-link to="/profile">
          <h4 class="account">
            <a href="#">Настройки</a>
          </h4>
        </router-link>
      </b-col>
      <b-col
        xl="12"
        cols="12"
        class="d-flex align-items-center justify-content-start pl-3 p-2 border-left border-right"
      >
        <img
          class="img-account"
          src="../img/account-img/account-news.svg"
          alt="NEWS"
        >
        <router-link to="/news">
          <h4 class="account">
            <a href>Новости</a>
          </h4>
        </router-link>
      </b-col>
      <b-col
        xl="12"
        cols="12"
        class="d-flex align-items-center justify-content-center pl-3 p-2 border-bottom border-left border-right"
      >
        <!-- <img
                    class="img-account"
                    src="../img/account-img/account-language.svg"
                    alt="LANGUAGE"
        />-->
        <h4 class="account text-center">
          <a
            href="#"
            @click="logout"
          >Выход</a>
        </h4>
      </b-col>
    </div>
  </b-row>
</template>

<script>
import LogReg from "./logAndReg.vue";
import axios from "axios";
export default {
    components: {
        LogReg
    },
    data: function() {
        return {
            user: {},
            log: this.$store.state.login,
            config: {
                headers: {
                    Authorization: "Token " + this.$cookies.get("user_session")
                }
            }
        };
    },
    mounted() {
        axios
            .get("http://bohmann.shop/api/user/", this.config)
            .then(response => (this.user = response.data));
    },
    methods: {
        logout() {
            console.log(this.config);
            axios
                .get("http://bohmann.shop/api/logout/", this.config)
                .then(
                    (this.$store.state.login = false),
                    this.$store.commit("saveCart"),
                    this.$cookies.remove("user_session")
                );
            window.location.reload();
        }
    }
};
</script>
<style>
.sz {
  width: 190px;
}
@media only screen and (max-width: 1199px) {
  .sz {
    width: 160px;
  }
}
</style>
