<template>
  <section class="nav-bohmann">
    <div class="img-logo rounded mx-auto d-block">
      <router-link to="/">
        <img class="img-fluid ww" src="@/img/logo_bohmann.svg" alt="logo" />
      </router-link>
    </div>
    <b-navbar toggleable="lg" type="light" class="nav-bohmann-bg__color ww p-0">
      <b-navbar-toggle target="nav-collapse" />
      <b-collapse id="nav-collapse" is-nav>
        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto mx-auto mt-2 mt-lg-0 p-2 pl-3 nav-bohmann-bg__colormenu">
          <ul class="navbar-nav nav d-flex align-items-center">
            <li class="nav-item dropdown mr-4 d-none d-lg-block">
              <b-nav-item-dropdown text="Lang">
                <template slot="button-content">
                  <img
                    class="img-settings"
                    draggable="false"
                    src="../img/nav-img/language-settings.svg"
                    alt
                  />
                </template>
                <b-dropdown-item href="#" disabled>RU</b-dropdown-item>
                <b-dropdown-item href="#" disabled>UA ( в разработке )</b-dropdown-item>
              </b-nav-item-dropdown>
            </li>
            <li class="nav-item mr-lg-5 mr-xl-5 mr-md-0">
              <router-link to="/sales">
                <a class="nav-link" href="#">Скидки</a>
              </router-link>
            </li>
            <li class="nav-item mx-lg-3 mx-xl-5 mx-md-0">
              <router-link to="/catalog">
                <a class="nav-link" href="#">Каталог</a>
              </router-link>
            </li>
            <li class="nav-item mx-lg-3 mx-xl-5 mx-md-0">
              <router-link to="/">
                <a class="nav-link" href="#">Главная</a>
              </router-link>
            </li>
            <div class="numb">{{ $store.state.cartCount }}</div>
            <li class="nav-item mx-lg-3 mx-xl-5 mx-md-0">
              <router-link to="/basket">
                <a class="nav-link" href="#">Корзина</a>
              </router-link>
            </li>
            <li class="nav-item ml-xl-4 ml-lg-4 mr-xl-3 mr-lg-3 m-md-0 pl-xl-3 pl-lg-3 pl-md-0">
              <router-link to="/news">
                <a class="nav-link" href="#">Новости</a>
              </router-link>
            </li>
            <li class="nav-item d-none d-lg-block">
              <b-nav-item-dropdown right :disabled="log">
                <b-dropdown-item>
                  <router-link to="/profile" style="color: black">Редактирование профиля</router-link>
                </b-dropdown-item>
                <template slot="button-content">
                  <img class="img-settings" draggable="false" src="../img/nav-img/settings.svg" alt />
                </template>
              </b-nav-item-dropdown>
            </li>
          </ul>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <div class="qq">
      <div class="line">
        <Slide>
          <router-link to="/">
            <img style="width:90%" src="@/img/mob_logo_bohmann.svg" alt />
          </router-link>
          <router-link to="/sales">
            <a class="hr" href="#">
              <span>Скидки</span>
            </a>
          </router-link>
          <router-link to="/catalog">
            <a class="hr" href="#">
              <span>Каталог</span>
            </a>
          </router-link>
          <router-link to="/">
            <a class="hr" href="#">
              <span>Главная</span>
            </a>
          </router-link>
          <router-link to="/basket">
            <a class="hr" href="#">
              <span>Корзина</span>
            </a>
            <div class="numbn">{{ $store.state.cartCount }}</div>
          </router-link>
          <router-link to="/news">
            <a class="hr" href="#">
              <span>Новости</span>
            </a>
          </router-link>
          <div v-if="log == 'false' || false">
            <LogReg class="hr" />
          </div>
          <div v-else class="hr">Добро пожаловать {{ user1.name }}!</div>
          <router-link to="/favorite">
            <h4 v-if="log == 'true'" class="account hr">
              <a href="#">Список желаний</a>
            </h4>
          </router-link>
          <router-link v-if="log == 'true'" to="/profile">
            <h4 class="account hr">
              <a href="#">Настройки</a>
            </h4>
          </router-link>
          <router-link v-if="log == 'true'" to="/news">
            <h4 class="account hr">
              <a href>Новости</a>
            </h4>
          </router-link>
          <router-link v-if="log == 'true'" to="/news">
            <h4 class="account hr mt-5">
              <a href @click="logout">Выйти</a>
            </h4>
          </router-link>
        </Slide>
      </div>
    </div>
  </section>
</template>

<script>
import { Slide } from "vue-burger-menu";
import axios from "axios";
import LogReg from "./logAndReg.vue";
export default {
  components: {
    Slide,
    LogReg
  },
  data: function() {
    return {
      user1: {},
      config: {
        headers: {
          Authorization: "Token " + this.$cookies.get("user_session")
        }
      },
      log: this.$store.state.login
    };
  },
  mounted() {
    axios
      .get("http://bohmann.shop/api/user/", this.config)
      .then(response => (this.user1 = response.data));
  },
  methods: {
    orderButton() {
      this.modalShow3 = !this.modalShow;
      axios
        .post("http://bohmann.shop/api/orders/", {
          user: this.user.id,
          name: "Помогите"
        })
        .then(response => (this.sup = response.data.id));
    },
    sendOrder() {
      axios.post(`http://bohmann.shop/api/tickets/${this.sup}/messages/`, {
        text: this.helpText,
        answer: false
      });
      this.$refs["my-modal3"].hide();
      this.helpText = "";
      this.labelTextButton = "Ваша заявка принята на рассмотрение";
    },
    logout() {
      console.log(this.config);
      axios
        .get("http://bohmann.shop/api/logout/", this.config)
        .then(
          (this.$store.state.login = false),
          this.$store.commit("saveCart"),
          this.$cookies.remove("user_session")
        );
      window.location.reload();
    }
  }
};
</script>

<style>
.qq {
  display: none !important;
}

@media only screen and (max-width: 995px) {
  .qq {
    display: block !important;
  }
  .ww {
    display: none !important;
  }
}
.bm-overlay {
  background: white !important;
}
.bm-menu {
  padding: 0;
  background-color: #14335e;
}
.hr,
.hr a {
  color: #fff !important;
  font-weight: 700;
  margin-left: 10px;
}
.line {
  width: 100%;
  height: 100px;
  background-color: #14335e;
}

.numbn {
  background-color: #a30000;
  border-radius: 20px;
  width: 20px;
  height: 20px;
  box-shadow: 0 2px 2px 1px rgba(0, 0, 0, 0.25);
  position: relative;
  right: 6px;
  bottom: 5px;
  color: #fff;
  font-size: 14px;
  text-align: center;
}
</style>
