<template>
  <div id="app">
    <router-view
      :info="info"
      :article="article"
      :last-comments="lastComments"
      :user="user"
    />
  </div>
</template>

<script>
import axios from "axios";
export default {
    data() {
        return {
            props: ["info", "article", "lastComments", "user"],
            info: [],
            article: [],
            lastComments: [],
            currentLoggedUser: {},
            config: {
                headers: {
                    Authorization: "Token " + this.$cookies.get("user_session")
                }
            },
            user: {}
        };
    },
    mounted() {
        if (this.$cookies.get("user_session") == "") {
            this.$store.state.login = false;
        }

        axios
            .get("http://bohmann.shop/api/products/")
            .then(response => (this.info = response.data.results));
        axios
            .get("http://bohmann.shop/api/articles/")
            .then(response => (this.article = response.data.results));
        axios
            .get("http://bohmann.shop/api/lastComments/")
            .then(response => (this.lastComments = response.data.results));
        axios
            .get("http://bohmann.shop/api/user/", this.config)
            .then(response => (this.user = response.data));
    }
};
</script>

<style>
@import "./style/style.css";
</style>
