/* eslint-disable no-console */
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
let favorite = window.localStorage.getItem('favorite');
let cart = window.localStorage.getItem('cart');
let cartCount = window.localStorage.getItem('cartCount');
let login = window.localStorage.getItem('login');
export default new Vuex.Store({
  state: {
    favorite: favorite ? JSON.parse(favorite) : [],
    cart: cart ? JSON.parse(cart) : [],
    cartCount: cartCount ? parseInt(cartCount) : 0,
    login: login ? String(login) : Boolean(0),
  },
  mutations: {
    addToCart(state, item) {
      let found = state.cart.find((product) => product.id == item.id);
      if (found) {
        found.quantity++;
        found.totalPrice = found.quantity * found.price;
      } else {
        state.cart.push(item);
        Vue.set(item, 'inCart', false);
        Vue.set(item, 'quantity', 1);
        Vue.set(item, 'totalPrice', item.price);
      }
      state.cartCount++;
      this.commit('saveCart');
    },
    removeFromCart(state, item) {
      let index = state.cart.indexOf(item);
      if (index > -1) {
        let product = state.cart[index];
        state.cartCount -= product.quantity;
        state.cart.splice(index, 1);
      }
      this.commit('saveCart');
    },

    delToCart(state, item) {
      let found = state.cart.find((product) => product.id == item.id);

      if (found) {
        found.quantity--;
        found.totalPrice = found.quantity * found.price;
      } else {
        state.cart.splice(item);
        Vue.set(item, 'quantity', 1);
        Vue.set(item, 'totalPrice', item.price);
      }

      state.cartCount--;
      this.commit('saveCart');
    },
    addToFavorite(state, item) {
      let foun = state.favorite.find((prod) => prod.id == item.id);
      if (foun) {
        foun.inFv = true;
      } else {
        state.favorite.push(item);
        Vue.set(item, 'inFavorite', true);
        Vue.set(item, 'quantity', 1);
        Vue.set(item, 'inFv', true);
      }
      state.disblCount++;

      this.commit('saveCart');
    },
    removeFromFavorite(state, item) {
      let index = state.favorite.indexOf(item);
      if (index > -1) {
        let product = state.favorite[index];
        state.favoriteCount -= product.quantity;
        state.favorite.splice(index, 1);
      }
      this.commit('saveCart');
    },
    saveCart(state) {
      window.localStorage.setItem('cart', JSON.stringify(state.cart));
      window.localStorage.setItem('cartCount', state.cartCount);
      window.localStorage.setItem('login', state.login);
      window.localStorage.setItem('favorite', JSON.stringify(state.favorite));
    },
  },
  actions: {},
  modules: {},
});
