<template>
  <div>
    <b-link
      v-b-modal.modal-center
      @click="modalShow = !modalShow"
    >
      Вход
    </b-link>
    <b-modal
      id="modal-center"
      ref="my-modal"
      v-model="modalShow"
      hide-footer
      centered
      title="Вход в интернет-магазин"
      ok-only
    >
      <b-form @submit.prevent="loginUser">
        <b-form-group
          type="text"
          label="Номер телефона"
        >
          <b-form-input
            v-model="username"
            required
            type="text"
          />
        </b-form-group>
        <b-form>
          <label for="text-password">Пароль</label>
          <b-input
            id="text-password"
            v-model="password"
            required
            type="password"
            class="form-control"
            aria-describedby="password-help-block"
          />
        </b-form>
        <b-form-group class="mt-2">
          <b-form-checkbox-group>
            <b-link
              v-b-modal.modal-multi-2
              @click="closeModal, (modalShow1 = !modalShow1)"
            >
              Забыли пароль?
            </b-link>
          </b-form-checkbox-group>
        </b-form-group>
        <b-button
          block
          type="submit"
          variant="primary"
          @click="loginUser"
        >
          OK
        </b-button>
      </b-form>
    </b-modal>
    <div />
    <router-link to="/registration">
      <a href>Регистрация</a>
    </router-link>

    <b-modal
      ref="my-modal2"
      v-model="modalShow1"
      hide-footer
      centered
      ok-only
      title="Забыли пароль?"
    >
      <b-form>
        <b-form-group
          type="email"
          label="Ваш email"
        >
          <b-form-input
            v-model="recoveryEmail"
            required
            type="email"
          />
        </b-form-group>
      </b-form>
      <b-button
        class="mt-3"
        block
        type="submit"
        variant="primary"
        @click="recoveryPassword"
      >
        Сбросить пароль
      </b-button>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";
export default {
    data() {
        return {
            modalShow: false,
            modalShow1: false,
            username: "",
            password: "",
            recoveryEmail: ""
        };
    },
    mounted: function() {
        this.$store.commit("saveCart");
    },
    methods: {
        closeModal() {
            this.$refs["my-modal"].hide();
            this.$refs["my-modal2"].hide();
            (this.username = ""), (this.password = "");
        },
        loginUser(e) {
            e.preventDefault();
            axios
                .post("http://bohmann.shop/api/login/", {
                    username: this.username,
                    password: this.password
                })
                .then(response => {
                    this.$store.state.login = true;
                    this.$store.commit("saveCart");

                    this.$cookies.set("user_session", `${response.data.token}`);
                    window.location.reload();
                })
                .catch(e => {
                    this.errors.push(e);
                });
            this.$refs["my-modal"].hide();
            (this.username = ""), (this.password = "");
        },
        recoveryPassword(e) {
            e.preventDefault();
            axios
                .post("http://bohmann.shop/api/reset-password/", {
                    email: this.recoveryEmail
                })
                .then(response => {
                    console.log(response);
                    window.location.reload();
                })
                .catch(e => {
                    this.errors.push(e);
                });
            this.$refs["my-modal"].hide();
            this.$refs["my-modal2"].hide();
            (this.username = ""), (this.password = ""), (this.recoveryEmail = "");
        }
    }
};
</script>

<style></style>
